import React from 'react'
import './Statistics.scss'


type StatisticsType = {
    color: string,
    title: string,
    text: string,
    count: number
}
export const Statistics = ({ color, text, title, count }: StatisticsType) => {
    return (
        <>
            <div className="statistics" style={{ background: `rgba(${color}, 0.08)` }}>
                <div className="statistics__top" style={{ background: color }}>
                    {title}
                </div>
                <div className="statistics__inner">
                    <div className="statistics__count" style={{ color: color }}>{count}</div>
                    <div className="statistics__text" style={{ color: color }}>{text}</div>
                </div>
            </div>
        </>
    )
}
