import axios from "axios"
import { Dispatch } from "redux"
import { UserAction, UserActionTypes } from "../types/users"


export const fetchUsers = () => {
    return async (dispatch: Dispatch<UserAction>) => {
        try {
            dispatch({ type: UserActionTypes.FETCH_USERS })
            const response = await axios.get("http://192.168.0.181:3001/users")
            dispatch({ type: UserActionTypes.FETCH_USERS_SUCCESS, payload: response.data })
        } catch (error) {
            dispatch({
                type: UserActionTypes.FETCH_USERS_ERROR,
                payload: 'An error occured while loading Users'
            })
        }
    }
}

export const setUsersID = (sort: boolean) => {
    return async (dispatch: Dispatch) => {
        try {
            dispatch({ type: UserActionTypes.FETCH_USERS })
            const response = await axios.get(`http://192.168.0.181:3001/users?_sort=id&_order=${sort ? 'desc' : 'asc'}`)
            dispatch({ type: UserActionTypes.FETCH_USERS_SUCCESS, payload: response.data })
        } catch (error) {
            dispatch({
                type: UserActionTypes.FETCH_USERS_ERROR,
                payload: 'An error occured while loading Users'
            })
        }
    }
}

