
export interface WordsState {
    words: any[],
    loading: boolean,
    error: null | string
}

export enum WordActionTypes {
    FETCH_WORDS = 'FETCH_WORDS',
    FETCH_WORDS_SUCCES = 'FETCH_WORDS_SUCCES',
    FETCH_WORDS_ERROR = 'FETCH_WORDS_ERROR'
}


interface FetchWordsActions {
    type: WordActionTypes.FETCH_WORDS
}
interface FetchWordsSuccessActions {
    type: WordActionTypes.FETCH_WORDS_SUCCES,
    payload: any[]
}
interface FetchWordsErrorActions {
    type: WordActionTypes.FETCH_WORDS_ERROR
    payload: string
}

export type WordsActions = FetchWordsActions | FetchWordsSuccessActions | FetchWordsErrorActions
