import React from 'react'
import { Link } from 'react-router-dom'
import { useTypedSelector } from '../../hooks/useTypeSelector'
import './Words.scss'
export const Words = () => {
    const { words, error } = useTypedSelector(({ words }) => words)

    if (error) {
        return (<h1>{error}</h1>)
    }
    return (
        <div className='words'>
            <div className="words__top">
                <div></div>
                <div className="words__top-name">
                    Name
                </div>
                <input type="text" placeholder="Search" className="words__top-search search" />
            </div>
            <div className="words__items">
                {words.map((item) => {
                    return (
                        <div key={item.id} className="words__item">
                            <img src={`${process.env.PUBLIC_URL}/assets/img/${item.img}`} alt="" className="words__item-img" />
                            <Link to={`/words/${item.id}`}> <div className="words__item-word">{item.usa}</div></Link>
                            <div className="words__item-delete">Delete</div>
                        </div>
                    )
                })}
            </div>
        </div>
    )
}
