import React, { useState } from 'react'
import classnames from 'classnames'
import './Users.scss'
import { Link } from 'react-router-dom'
import { useTypedSelector } from '../../hooks/useTypeSelector'
import { useActions } from '../../hooks/useActions'



export const Users = () => {
    const { error, users } = useTypedSelector(state => state.users)
    const [id, setId] = useState(true)
    const [username, setUsername] = useState(true)
    const [words, setWords] = useState(true)
    const { setUsersID, setUsersName,setUsersWords } = useActions()

    if (error) {
        return <h1>{error}</h1>
    }


    const sortUserId = () => {
        setUsersID(id)
        setId(prev => !prev)
    }
    const sortUserUsername = () => {
        setUsersName(username)
        setUsername(prev => !prev)

    }
    const sortUserWords = () => {
        setUsersWords(words)
        setWords(prev => !prev)
    }
    return (
        <div className="users">
            <div className="users__inner">
                <div className="users__top">
                    <div className={classnames('users__top-id', { 'active': !id })} onClick={() => sortUserId()}>ID</div>
                    <div className={classnames('users__top-username', { 'active': !username })} onClick={() => sortUserUsername()}>Username</div>
                    <div className={classnames('users__top-words', {'active': !words})} onClick={()=> sortUserWords()}>Words</div>
                    <div className="users__top-last">Last seen</div>
                    <input type="text" placeholder="Search" className="users__top-search search" />
                </div>
                <div className="users__items">
                    {users && users.map((item) => {
                        const { id, phoneplatform, username, words, lastseen } = item
                        const date = +new Date()
                        const min = Math.floor(((date - lastseen) / 60000) % 60),
                            h = Math.floor(((date - lastseen) / 6000000) % 23)
                        return (
                            <div key={id} className="users__item">
                                <div className={classnames('users__item-id', { 'android': phoneplatform === 'android' })}>{id}</div>
                                <Link to={`/users/${id}`}><div className="users__item-username">{username}</div></Link>
                                <div className="users__item-words">{words}</div>
                                <div className="users__item-last">{h >= 1 ? h + ' h' : ''} {min} m ago</div>
                            </div>
                        )
                    })}

                </div>
            </div>

        </div>
    )
}
