import React from 'react'
import { useTypedSelector } from '../../../hooks/useTypeSelector'
import bedSvg from '../../../assets/img/bed.svg'
import './UserDetails.scss'

export const UserDetails = (props: any) => {
    const id = +props.match.params.id
    const { users, loading, error } = useTypedSelector(({ users }) => users)

    const user = users.filter((user) => user.id === id)[0]
    
    
    if (!loading) {
        return (<h1>{loading}</h1>)
    }
    if (error) {
        return (<h1>{error}</h1>)
    }
    return (
        <div className='userdetails'>
            <div className="userdetails__top">
                <div className="userdetails__username">
                    <div className="userdetails__username-title">{user.username}</div>
                    <div className="userdetails__username-id">ID {user.id}</div>
                    <div className="userdetails__username-datereg">Reg date: {user.datereg}</div>
                    <div className="userdetails__username-platform">From: {user.phoneplatform}</div>
                </div>
                <div className="userdetails__info">
                    <div className="userdetails__info-language ru">Selected language</div>
                    <div className="userdetails__info-answers">Correct answers 92%<div className="userdetails__info-answers-progress" style={{ width: '92%' }}></div></div>
                </div>
            </div>
            <div className="userdetails__box">
                <div className="userdetails__sort">
                    <div className="userdetails__sort-answers">Answers</div>
                    <div className="userdetails__sort-correct">Correct</div>
                    <div className="userdetails__sort-date">Date</div>
                </div>
                <div className="userdetails__items">
                    <div className="userdetails__item">
                        <div className="userdetails__item-img"><img src={bedSvg} alt="" /></div>
                        <div className="userdetails__item-answers">Bed</div>
                        <div className="userdetails__item-correct"></div>
                        <div className="userdetails__item-date">20.08.20</div>
                    </div>
                    <div className="userdetails__item">
                        <div className="userdetails__item-img"><img src={bedSvg} alt="" /></div>
                        <div className="userdetails__item-answers">Bed</div>
                        <div className="userdetails__item-correct"></div>
                        <div className="userdetails__item-date">20.08.20</div>
                    </div>
                    <div className="userdetails__item">
                        <div className="userdetails__item-img"><img src={bedSvg} alt="" /></div>
                        <div className="userdetails__item-answers">Bed</div>
                        <div className="userdetails__item-correct"></div>
                        <div className="userdetails__item-date">20.08.20</div>
                    </div>
                    <div className="userdetails__item">
                        <div className="userdetails__item-img"><img src={bedSvg} alt="" /></div>
                        <div className="userdetails__item-answers">Bed</div>
                        <div className="userdetails__item-correct"></div>
                        <div className="userdetails__item-date">20.08.20</div>
                    </div>
                    <div className="userdetails__item">
                        <div className="userdetails__item-img"><img src={bedSvg} alt="" /></div>
                        <div className="userdetails__item-answers">Bed</div>
                        <div className="userdetails__item-correct"></div>
                        <div className="userdetails__item-date">20.08.20</div>
                    </div>
                    <div className="userdetails__item disabled">
                        <div className="userdetails__item-img"><img src={bedSvg} alt="" /></div>
                        <div className="userdetails__item-answers">Bed</div>
                        <div className="userdetails__item-correct">afaedg</div>
                        <div className="userdetails__item-date">20.08.20</div>
                    </div>
                    <div className="userdetails__item">
                        <div className="userdetails__item-img"><img src={bedSvg} alt="" /></div>
                        <div className="userdetails__item-answers">Bed</div>
                        <div className="userdetails__item-correct"></div>
                        <div className="userdetails__item-date">20.08.20</div>
                    </div>
                </div>
            </div>
        </div>
    )
}
