import { WordActionTypes, WordsActions, WordsState } from "../types/words"

const initialState: WordsState = {
    words: [],
    loading: false,
    error: null
}

export const wordsReducer = (state = initialState, action: WordsActions): WordsState => {
    switch (action.type) {
        case WordActionTypes.FETCH_WORDS:
            return { ...state, loading: false, error: null }
        case WordActionTypes.FETCH_WORDS_SUCCES:
            return { ...state, loading: true, error: null, words: action.payload }
        case WordActionTypes.FETCH_WORDS_ERROR:
            return { ...state, loading: false, error: action.payload }
        default:
            return state
    }
}