import React from 'react'
import { useTypedSelector } from '../../../hooks/useTypeSelector'
import './WordDetails.scss'

import ruSvg from '../../../assets/img/rus.svg'
import finSvg from '../../../assets/img/fin.svg'
import deSvg from '../../../assets/img/de.svg'
import frSvg from '../../../assets/img/fr.svg'
import spaSvg from '../../../assets/img/spa.svg'
import usaSvg from '../../../assets/img/usa.svg'
export const WordDetails = (props: any) => {

    const { words, loading, error } = useTypedSelector(({ words }) => words)
    const id = +props.match.params.id
    const word = words.filter((item) => item.id === id)[0]

    if(!loading) {
        return(<h1>{loading}</h1>)
    }
    if(error) {
        return(<h1>{error}</h1>)
    }
    const disabledEventForm:React.FormEventHandler = (e) => {
        e.preventDefault()
    }
    return (
        <div className='worddetails'>
            <div className="worddetails__title">{word.usa}</div>
            <div className="worddetails__inner">
                <img src={`${process.env.PUBLIC_URL}/assets/img/${word.img}`} alt="" className="worddetails__img" />
                <form onSubmit={(e) => disabledEventForm(e)} className="worddetails__change">
                    <div className="worddetails__change-input">
                        <label htmlFor="usa">
                            <img src={usaSvg} alt="" />
                            <input id="usa" type="text" placeholder={word.usa} />
                        </label>
                    </div>
                    <div className="worddetails__change-input">
                        <label htmlFor="spa">
                            <img src={spaSvg} alt="" />
                            <input id="spa" type="text" placeholder={word.spa} />
                        </label>
                    </div>
                    <div className="worddetails__change-input">
                        <label htmlFor="de">
                            <img src={deSvg} alt="" />
                            <input id="de" type="text" placeholder={word.de} />
                        </label>
                    </div>
                    <div className="worddetails__change-input">
                        <label htmlFor="fi">
                            <img src={finSvg} alt="" />
                            <input id="fi" type="text" placeholder={word.fi} />
                        </label>
                    </div>
                    <div className="worddetails__change-input">
                        <label htmlFor="fr">
                            <img src={frSvg} alt="" />
                            <input id="fr" type="text" placeholder={word.fr} />
                        </label>
                    </div>
                    <div className="worddetails__change-input">
                        <label htmlFor="rus">
                            <img src={ruSvg} alt="" />
                            <input id="rus" type="text" placeholder={word.rus} />
                        </label>
                    </div>
                    <button type="submit" className="worddetails__change-button">Save changes</button>
                </form>
            </div>
        </div>
    )
}
