import axios from "axios"
import { Dispatch } from "redux"
import { WordActionTypes, WordsActions } from "../types/words"



export const fetchWords = () => {
    return async (dispatch: Dispatch<WordsActions>) => {
        try {
            dispatch({ type: WordActionTypes.FETCH_WORDS })
            const response = await axios.get('http://192.168.0.181:3001/words')
            dispatch({
                type: WordActionTypes.FETCH_WORDS_SUCCES,
                payload: response.data
            })
        } catch (error) {
            dispatch({
                type: WordActionTypes.FETCH_WORDS_ERROR,
                payload: 'An error occured while loading Words'
            })
        }
    }
}