import React from 'react'
import { Language, Statistics } from '../../components'
import { Events } from '../../components/Events/Events'
import './Dashboard.scss'

export const Dashboard = () => {
    return (
        <div className='dashboard'>
            <div className="dashboard__inner">
                <div className="dashboard__left">
                    <Events />
                </div>
                <div className="dashboard__right">
                    <div className="dashboard__right-top">
                        <Statistics color='#209E52' text='new users per week' title='Users' count={646} />
                        <Statistics color='#D169CD' text='correct answers' title='Total' count={3059} />
                    </div>
                    <Language />
                </div>
            </div>
        </div>
    )
}
